import { FlashMessageService } from 'src/app/services/flash-message.service';
import { Message } from 'src/app/models/message';
import { Component, OnInit } from '@angular/core';
import { ApiService } from './../../../services/api.service';

@Component({
  selector: 'app-edit-model',
  templateUrl: './edit-model.component.html',
  styleUrls: ['./edit-model.component.css']
})
export class EditModelComponent implements OnInit {

  public markName = [];
  public modelsName = [];
  public carId: String;
  public modelId: String;
  public isConfirmed = false;
  public modelName: String;
  public message: Message;

  constructor(
    private _apiService: ApiService,
    private _flashMessagesService: FlashMessageService
  ) { }

  ngOnInit() {
    //      
    this.getCars();
  }
  //   
  public getCars(): void {
    this._apiService.getCars()
      .subscribe(data => {
        this.markName = data;
      },
        error => {
          this._flashMessagesService.showMessage(error).subscribe(data => this.message = data);
        }
      );
  }

  //  Id  .   
  public passCarId(event): void {
    this.carId = event.target.value;
    this._apiService.getModelName(this.carId)
      .subscribe(data => {
        this.modelsName = data;
      },
        error => {
          this._flashMessagesService.showMessage(error).subscribe(data => this.message = data);
        }
      );
  }

  //  Id  .
  public passModelId(event): void {
    this.modelId = event.target.value;
    this.isConfirmed = true;
  }

  //    ,   ,
  //    ,  ,      
  public editModel() {
    const model = {
      modelsName: this.modelName
    };
    if (this.carId && this.modelId) {
      this._apiService.editModel(this.carId, this.modelId, model)
        .subscribe(data => {
          this._flashMessagesService.showMessage('  ', 'success', 3000).subscribe(msg => {
            this.message = msg;
          });
        },
          error => {
            this._flashMessagesService.showMessage(error).subscribe(data => this.message = data);
          });
      this.modelName = '';
    } else {
      this._flashMessagesService.showMessage('  ').subscribe(data => this.message = data);
    }
  }

  //   
  public remove() {
    if (this.carId && this.modelId) {
      this._apiService.removeModel(this.carId, this.modelId)
        .subscribe(data => {
          this._flashMessagesService.showMessage('  ', 'success', 3000).subscribe(msg => {
            this.message = msg;
          });
        },
          error => {
            this._flashMessagesService.showMessage(error).subscribe(data => this.message = data);
          });
    } else {
      this._flashMessagesService.showMessage('  ').subscribe(data => this.message = data);
    }
  }
}
